import React from "react"
import { formatDistanceToNow, parseISO } from 'date-fns'

//Hooks
import useHooks from './hooks/use-hooks.js';

//Material UI Components
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@mui/material/Tooltip';
import Grid from '@material-ui/core/Grid';

import FavoriteIcon from '@mui/icons-material/Favorite';
import PersonIcon from '@mui/icons-material/Person';
import StorageIcon from '@mui/icons-material/Storage';
import SettingsIcon from '@mui/icons-material/Settings';
import FactCheckIcon from '@mui/icons-material/FactCheck';
import ExtensionIcon from '@mui/icons-material/Extension';
import CheckIcon from '@mui/icons-material/Check';
import ClearIcon from '@mui/icons-material/Clear';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';

//Styles
import useStyles from './my-bim-tracker-styles.js';

export default function MyBIMTracker(props) {
    const {
        userData
    } = props;
    const {
        myRecords,
        bimLeadRecords,
        bimCheckerApproverRecords,
        handleExpandTables,
        expandTables,
        handleOpenProject,
        handleOpenProjectDiscipline
    } = useHooks(
        userData
    );
    const classes = useStyles();
    return (
        <div>
            <Grid container direction="row" alignItems="center">
                <Grid item xs={11}>
                    <Typography variant="h6">BIM Reviewer</Typography>
                </Grid>
                <Grid item xs={1}>
                    <Tooltip title={
                        <span className={classes.helpTooltipSpan}>
                            This Card contains relevant progress information for your BIM projects, open the tabs and click on any ticks
                            or crosses to be taken to the relevant section of the BIM Tracker. You'll find more tooltips throughout, that
                            provide explanations where needed.
                        </span>}
                        placement="bottom-end"
                    >
                        <HelpOutlineIcon fontSize="small" className={classes.helpIcon} />
                    </Tooltip>
                </Grid>
            </Grid>
            <div className={classes.accordionContainer}>
            {bimCheckerApproverRecords.length > 0 && <>
            <Typography variant="subtitle1" align="left" className={classes.subtitle}>I'm a Checker or Approver:</Typography>
            {bimCheckerApproverRecords.map((record) => (
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon fontSize="small"/>}
                    >
                        <div className={classes.accordionProjectStatus}>
                        <Tooltip placement="top" title={100 - record.projectStatus < 0
                            ? 0 + "%"
                            : 100 - record.projectStatus + "%"}
                        >
                            <FavoriteIcon fontSize="small" className={100 - record.projectStatus < 51 ? (classes.cellRed) : 
                                (100 - record.projectStatus < 76 ? (classes.cellAmber) : 
                                (100 - record.projectStatus < 100 ? (classes.cellYellow) : (classes.cellGreen)))}
                            />
                        </Tooltip>
                        </div>
                        <Typography variant="body2" sx={{width: "90%"}}>
                            {record.PCode} {record.JobNameShort}
                        </Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Table>
                            <TableHead>
                                <TableCell className={classes.iconHeader}><Tooltip title="People" placement="top"><PersonIcon /></Tooltip></TableCell>
                                <TableCell className={classes.iconHeader}><Tooltip title="Storage" placement="top"><StorageIcon /></Tooltip></TableCell>
                                <TableCell className={classes.iconHeader}><Tooltip title="Process" placement="top"><SettingsIcon /></Tooltip></TableCell>
                                <TableCell className={classes.iconHeader}><Tooltip title="Coordination" placement="top"><FactCheckIcon /></Tooltip></TableCell>
                                <TableCell className={classes.iconHeader}><Tooltip title="Automation" placement="top"><ExtensionIcon /></Tooltip></TableCell>
                                <TableCell className={classes.tableHeader}>Last Updated</TableCell>
                            </TableHead>
                            <TableBody>
                                <TableRow>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "people")}>{record.completePeople ? (<CheckIcon />) : (<ClearIcon />)}</TableCell>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "storage")}>{record.completeStorage ? (<CheckIcon />) : (<ClearIcon />)}</TableCell>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "process")}>{record.completeProcess ? (<CheckIcon />) : (<ClearIcon />)}</TableCell>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "coordination")}>{record.completeCoordination ? (<CheckIcon />) : (<ClearIcon />)}</TableCell>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "automation")}>
                                        {record.automation_Clarity_Nightly_Exports ? (<CheckIcon />) : (<ClearIcon />)}
                                    </TableCell>
                                    <TableCell>{record.updated_at ? (formatDistanceToNow(parseISO(record.updated_at))) + " ago" : ("No data")}</TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </AccordionDetails>
                </Accordion>
            ))}
            </>}
            {bimLeadRecords.length > 0 && <>
            <Typography variant="subtitle1" align="left" className={classes.subtitle}>I'm a BIM Lead:</Typography>
            {bimLeadRecords.map((record) => (
                <Accordion key={record.PCode}>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon fontSize="small" />}
                    >
                        <div className={classes.accordionProjectStatus}>
                        <Tooltip placement="top" title={100 - record.projectStatus < 0
                            ? 0 + "%"
                            : 100 - record.projectStatus + "%"}
                        >
                            <FavoriteIcon fontSize="small" className={100 - record.projectStatus < 51 ? (classes.cellRed) : 
                                (100 - record.projectStatus < 76 ? (classes.cellAmber) : 
                                (100 - record.projectStatus < 100 ? (classes.cellYellow) : (classes.cellGreen)))}
                            />
                        </Tooltip>
                        </div>
                        <Typography variant="body2">
                            {record.PCode} {record.JobNameShort}
                        </Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Table>
                            <TableHead>
                                <TableCell className={classes.iconHeader}><Tooltip title="People" placement="top"><PersonIcon /></Tooltip></TableCell>
                                <TableCell className={classes.iconHeader}><Tooltip title="Storage" placement="top"><StorageIcon /></Tooltip></TableCell>
                                <TableCell className={classes.iconHeader}><Tooltip title="Process" placement="top"><SettingsIcon /></Tooltip></TableCell>
                                <TableCell className={classes.iconHeader}><Tooltip title="Coordination" placement="top"><FactCheckIcon /></Tooltip></TableCell>
                                <TableCell className={classes.iconHeader}><Tooltip title="Automation" placement="top"><ExtensionIcon /></Tooltip></TableCell>
                                <TableCell className={classes.tableHeader}>Last Updated</TableCell>
                            </TableHead>
                            <TableBody>
                                <TableRow>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "people")}>{record.completePeople ? (<CheckIcon />) : (<ClearIcon />)}</TableCell>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "storage")}>{record.completeStorage ? (<CheckIcon />) : (<ClearIcon />)}</TableCell>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "process")}>{record.completeProcess ? (<CheckIcon />) : (<ClearIcon />)}</TableCell>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "coordination")}>{record.completeCoordination ? (<CheckIcon />) : (<ClearIcon />)}</TableCell>
                                    <TableCell className={classes.iconCell} onClick={() => handleOpenProjectDiscipline(record.PCode, "automation")}>
                                        {record.automation_Clarity_Nightly_Exports ? (<CheckIcon />) : (<ClearIcon />)}
                                    </TableCell>
                                    <TableCell>{record.updated_at ? (formatDistanceToNow(parseISO(record.updated_at))) + " ago" : ("No data")}</TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </AccordionDetails>
                </Accordion>
            ))}
            </>}
            </div>
            {bimLeadRecords.length === 0 && bimCheckerApproverRecords.length === 0 &&
            <Typography variant="body2">
                It looks like you aren't on any BIM Projects<br />
                Visit the BIM Tracker <a href="https://bmtools.arup.com/bimtracker/" target="_blank" rel="noreferrer" >here</a> and check back later.
            </Typography>
            }
            
        </div>
    );
}