import React from "react"

//Material UI Components
import Typography from '@material-ui/core/Typography';
import Tooltip from '@mui/material/Tooltip';
import Grid from '@material-ui/core/Grid';
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import IconButton from "@material-ui/core/IconButton";

import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import SettingsIcon from '@mui/icons-material/Settings';
import BorderColorIcon from '@mui/icons-material/BorderColor';
import CalendarTodayIcon from '@mui/icons-material/CalendarToday';
import DownloadIcon from '@mui/icons-material/Download';
import TrendingUpIcon from '@mui/icons-material/TrendingUp';
import FavoriteIcon from '@mui/icons-material/Favorite';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import ErrorIcon from '@mui/icons-material/Error';
import NotInterestedIcon from '@mui/icons-material/NotInterested';
import HelpIcon from '@mui/icons-material/Help';

//Hooks
import useHooks from './hooks/use-hooks.js';

//Styles
import useStyles from './ipp-dashboard-styles.js';

export default function IPPDashboard(props) {
    const {
        userData,
        adsToken
    } = props;
    const classes = useStyles();
    const {
        myIPPData,
        buildURL
    } = useHooks(userData, adsToken);

    const ExpandableTableRow = ({ children, expandComponent, ...otherProps }) => {
        const [isExpanded, setIsExpanded] = React.useState(false);
        return (
          <>
            <TableRow {...otherProps}>
              <TableCell padding="checkbox">
                <IconButton onClick={() => setIsExpanded(!isExpanded)}>
                  {isExpanded ? <KeyboardArrowUpIcon fontSize="small"/> : <KeyboardArrowDownIcon fontSize="small"/>}
                </IconButton>
              </TableCell>
              {children}
            </TableRow>
            {isExpanded && (
              <TableRow>
                <TableCell padding="checkbox" />
                {expandComponent}
              </TableRow>
            )}
          </>
        );
      };

      const statusIcon = (status) => {
        if (status === "Ok") {
            return <CheckCircleIcon sx={{color: "green"}} fontSize="small"/>
        }
        else if (status === "Warning") {
            return <HelpIcon sx={{color: "#e6e600", }} fontSize="small"/>
        }
        else if (status === "Critical") {
            return <ErrorIcon sx={{color: "red"}} fontSize="small"/>
        }
        else {
            return <NotInterestedIcon fontSize="small" />
        }
      }

    return (
        <div>
            <Grid container direction="row" alignItems="center">
                <Grid item xs={11}>
                    <Typography variant="h6">IPP Dashboard</Typography>
                </Grid>
                <Grid item xs={1}>
                    <Tooltip title={
                        <span className={classes.helpTooltipSpan}>
                            This Card contains a summary of your projects on IPP along with their status
                            and messages. Click on each status icon to go to the relevant section of that
                            project on IPP.
                        </span>}
                        placement="bottom-end"
                    >
                        <HelpOutlineIcon fontSize="small" className={classes.helpIcon} />
                    </Tooltip>
                </Grid>
            </Grid>
            <Paper className={classes.tableContainer}>
                <Table stickyHeader>
                    <TableHead>
                        <TableRow>
                            <TableCell />
                            <TableCell>Job No</TableCell>
                            <TableCell><Tooltip title="Setup & Admin" placement="top"><SettingsIcon fontSize="small"/></Tooltip></TableCell>
                            <TableCell><Tooltip title="Agreements" placement="top"><BorderColorIcon fontSize="small"/></Tooltip></TableCell>
                            <TableCell><Tooltip title="Project Reviews" placement="top"><CalendarTodayIcon fontSize="small"/></Tooltip></TableCell>
                            <TableCell><Tooltip title="Delivery" placement="top"><DownloadIcon fontSize="small"/></Tooltip></TableCell>
                            <TableCell><Tooltip title="Monitoring" placement="top"><FavoriteIcon fontSize="small"/></Tooltip></TableCell>
                            <TableCell><Tooltip title="Improvement" placement="top"><TrendingUpIcon fontSize="small"/></Tooltip></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                    {myIPPData.map((row) => (
                        <ExpandableTableRow
                            key={row.projectNo}
                            expandComponent={
                                <TableCell colSpan="7">
                                    {row.messages.map((message) => (
                                    <Grid container direction="row" spacing={2}>
                                        <Grid item xs={4}>{message.name}</Grid>
                                        <Grid item xs={8}>{message.message}</Grid>
                                    </Grid>
                                    ))}
                                </TableCell>
                            }
                        >
                            <TableCell>{row.projectNo}</TableCell>
                            <TableCell onClick={() => buildURL(row.projectNo, "setup")} className={classes.statusCell}>{statusIcon(row.SetupSection)}</TableCell>
                            <TableCell onClick={() => buildURL(row.projectNo, "agreements")} className={classes.statusCell}>{statusIcon(row.AgreementsSection)}</TableCell>
                            <TableCell onClick={() => buildURL(row.projectNo, "reviews")} className={classes.statusCell}>{statusIcon(row.ReviewsSection)}</TableCell>
                            <TableCell onClick={() => buildURL(row.projectNo, "delivery")} className={classes.statusCell}>{statusIcon(row.DeliverySection)}</TableCell>
                            <TableCell onClick={() => buildURL(row.projectNo, "monitoring")} className={classes.statusCell}>{statusIcon(row.MonitoringSection)}</TableCell>
                            <TableCell onClick={() => buildURL(row.projectNo, "improvement")} className={classes.statusCell}>{statusIcon(row.ImprovementSection)}</TableCell>
                        </ExpandableTableRow>
                    ))}
                    </TableBody>
                </Table>
            </Paper>
        </div>
    );
}