import React from "react"

//Material UI Components
import Box from '@mui/material/Box';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Popover from '@mui/material/Popover';
import Button from '@mui/material/Button';
import Grid from '@material-ui/core/Grid';
import PersonIcon from '@mui/icons-material/Person';

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Divider from '@mui/material/Divider';
import Avatar from '@mui/material/Avatar';

//Hooks
import useHooks from './hooks/use-hooks.js';

//Styles
import useStyles from './header-styles.js'

export default function Header(props) {
    const classes = useStyles();
    const {
        userData,
        userImgData,
        selectedBackground,
        setSelectedBackground
    } = props;
    const {
        togglePopover,
        popoverOpen,
        closePopover,
        anchorEl,
        handleChangeBackground,
    }=useHooks(selectedBackground, setSelectedBackground);

    return (
        <Box sx={{ flexGrow: 1 }} className={classes.box}>
            <AppBar position="fixed" style={{backgroundColor: "#e3e3e3"}}>{/* was static OR used fixed with a spaceDiv*/}
                <Toolbar className={classes.toolbar} disableGutters>
                    <Grid container>
                        <Grid item xs={4} />
                        <Grid item xs={4}>
                            <Typography variant="h4" className={classes.title}>My Hub</Typography>
                        </Grid>
                        <Grid item xs={2} />
                        <Grid item xs={2}>
                            <Button 
                                onClick={togglePopover}
                                variant="contained"
                                className={classes.popoverButton}
                                startIcon={<PersonIcon />}
                            >
                                {/*Add user profile image or blank person icon*/}
                                Account
                            </Button>
                            <Popover
                                open={popoverOpen}
                                anchorEl={anchorEl}
                                onClose={closePopover}
                                anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'right',
                                }}
                                transformOrigin={{
                                    vertical: 'top',
                                    horizontal: 'right',
                                }}
                                className={classes.popover}
                                disableScrollLock
                            >
                                <Grid container alignItems="center" className={classes.popover}>
                                    <Grid item xs={4}>
                                        {userImgData ? 
                                            <Avatar src={userImgData} sx={{width:50, height:50}} className={classes.userAvatar} />
                                        : 
                                            <PersonIcon fontSize="large" className={classes.userAvatar} />
                                        }
                                    </Grid>
                                    <Grid item xs={8}>
                                        <Typography>{userData.displayName}</Typography>
                                        <Typography>{userData.officeLocation}</Typography>
                                    </Grid>
                                </Grid>
                                <Divider />
                                <div className={classes.backgroundForm}>
                                <FormControl component="fieldset">
                                    <Typography>Background Wallpaper</Typography>
                                    <RadioGroup
                                        aria-label="background"
                                        name="controlled-radio-buttons-group"
                                        value={selectedBackground}
                                        onChange={handleChangeBackground}
                                    >
                                        <FormControlLabel value="/static/media/Lemons.25a0929a.png" control={<Radio />} label="Lemons" />
                                        <FormControlLabel value="/static/media/Cosmos.c6c9718e.png" control={<Radio />} label="Cosmos" />
                                        <FormControlLabel value="/static/media/Koi.a797714b.png" control={<Radio />} label="Koi" />
                                        <FormControlLabel value="/static/media/Circuit.6d04891f.png" control={<Radio />} label="Circuit" />
                                    </RadioGroup>
                                </FormControl>
                                </div>
                            </Popover>
                        </Grid>
                    </Grid>
                </Toolbar>
            </AppBar>
        </Box>
    );
}