import React from "react"

//Material UI Components
import Button from '@material-ui/core/Button';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@mui/material/Tooltip';
import Grid from '@material-ui/core/Grid';

import HelpOutlineIcon from '@mui/icons-material/HelpOutline';

//Hooks
import useHooks from './hooks/use-hooks.js';

//Styles
import useStyles from './my-calcs-styles.js'

export default function MainHub(props) {
    const {
        userData
    }= props;
    const {
        myCalcs,
        completeCalc
    } = useHooks(userData);
    const classes = useStyles();
    return (
        <div>
            <Grid container direction="row" alignItems="center">
                <Grid item xs={11}>
                    <Typography variant="h6">Calc Centre</Typography>
                </Grid>
                <Grid item xs={1}>
                    <Tooltip title={
                        <span className={classes.helpTooltipSpan}>
                            This Card contains calculations that have been assigned to you and not yet marked
                            as completed. Click on a calculation to be taken to the Calculation Tracker and
                            begin work.
                        </span>}
                        placement="bottom-end"
                    >
                        <HelpOutlineIcon fontSize="small" className={classes.helpIcon} />
                    </Tooltip>
                </Grid>
            </Grid>
            {myCalcs.length > 0 ? (
            <TableContainer component={Paper} className={classes.table}>
                <Table stickyHeader size="small">
                    <TableHead>
                        <TableCell>Calculation Name</TableCell>
                        <TableCell>Project Name</TableCell>
                        <TableCell>Stage</TableCell>
                        <TableCell />
                    </TableHead>
                    <TableBody>
                        {myCalcs.map((calc) => (
                         <TableRow key={calc.calculation_id}>
                             <TableCell>{calc.calculation}</TableCell>
                             <TableCell>{calc.JobNameShort}</TableCell>
                             <TableCell>{calc.stage}</TableCell>
                             <TableCell><Button variant="contained" color="primary" onClick={() => completeCalc(calc)}>Complete</Button></TableCell>
                         </TableRow>
                    ))}
                    </TableBody>
                </Table>
            </TableContainer>
            ) : (
            <Typography variant="body2">
                It looks like you don't have any calculations to Complete. <br />
                Visit the Calc Tracker and check back later.<br />
                <a href="https://bmtools.arup.com/calcs/mechanical" target="_blank" rel="noreferrer" >Mechanical</a> |&nbsp;
                <a href="https://bmtools.arup.com/calcs/electrical" target="_blank" rel="noreferrer" >Electrical</a> |&nbsp;
                <a href="https://bmtools.arup.com/calcs/structural" target="_blank" rel="noreferrer" >Structural</a>
            </Typography>   
            )}
        </div>
    );
}