import React from "react"
import MicrosoftLogin from "react-microsoft-login";

//Material UI Components
import Grid from '@material-ui/core/Grid';
import Card from '@mui/material/Card';
import Alert from '@mui/material/Alert';

//Hooks
import useHooks from './hooks/use-hooks.js';

//Project Components
import MyCalcs from '../my-calcs';
import MyBIMTracker from '../my-bim-tracker';
import BMWeather from "../bm-weather";
import BMNews from '../bm-news';
import Header from '../header';
import MyProjects from '../my-projects';
import Welcome from '../welcome';
import ComingSoon from '../coming-soon';
import IPPDashboard from '../ipp-dashboard';

//Styles
import useStyles from './main-hub-styles';

export default function MainHub() {
    const {
        handleNotification,
        loggedIn,
        authHandler,
        userData,
        userImgData,
        selectedBackground,
        setSelectedBackground,
        adsToken
    } = useHooks();
    const classes = useStyles();
    return (
        <div>
        {!loggedIn &&
        <>
            <Alert severity="warning">Please ensure you are connected to the Arup Network for a full experience</Alert>
            <MicrosoftLogin clientId="48737b9c-5041-4880-b9b3-0e58e74bd446" tenantUrl="https://login.microsoftonline.com/4ae48b41-0137-4599-8661-fc641fe77bea" authCallback={authHandler} className={classes.loginScreen} useLocalStorageCache withUserData/>
            {/* Arup Hub clientId="2b4cf606-ac2c-4532-a7b0-483d7f2427cc" */}
        </>
        }
        {loggedIn && 
        <div className={classes.wholePage} style={{backgroundImage: `url(${selectedBackground})`}}>
            <Header
                userData={userData}
                userImgData={userImgData}
                selectedBackground={selectedBackground}
                setSelectedBackground={setSelectedBackground}
            />
            <div className={classes.spacerDiv}/>
            <div className={classes.content}>
                <Grid container direction="row" spacing={2} className={classes.contentGridRow}>
                    <Grid item xs={7}>
                        <Card>
                            <MyCalcs userData={userData} />
                        </Card>
                    </Grid>
                    <Grid item xs={5}>
                        <Card>
                            <Welcome />
                        </Card>
                    </Grid>
                </Grid>
                <Grid container direction="row" spacing={2} className={classes.contentGridRow}>
                    <Grid item xs={7}>
                        <Card>
                            <MyBIMTracker 
                                userData={userData}
                            />
                        </Card>
                    </Grid>
                    <Grid item xs={5}>
                        <Card>
                            <MyProjects
                                userData={userData}
                            />
                        </Card>
                    </Grid>
                </Grid>
                <Grid container direction="row" spacing={2} className={classes.contentGridRow}>
                    <Grid item xs={5}>
                        <Card>
                            <BMNews />
                        </Card>
                    </Grid>
                    <Grid item xs={7}>
                        <Card>
                            <IPPDashboard 
                                userData={userData}
                                adsToken={adsToken}
                            />
                        </Card>
                    </Grid>
                </Grid>
                <Grid container direction="row" spacing={2} className={classes.contentGridRow}>
                    <Grid item xs={6}>
                        <Card>
                            <ComingSoon />
                        </Card>
                    </Grid>
                    <Grid item xs={6}>
                        <Card>

                        </Card>
                    </Grid>
                </Grid>
            </div>
        </div>
        }
        </div>
    );
}