import { useState, useEffect } from "react";
const axios = require("axios");

export default (userData) => {
  const [myRecords, setMyRecords] = useState([]);
  const [bimLeadRecords, setBimLeadRecords] = useState([]);
  const [bimCheckerApproverRecords, setBimCheckerApproverRecords] = useState(
    []
  );
  const [expandTables, setExpandTables] = useState(false);

  const getRecords = () => {
    axios
      .get("https://bmtools.arup.com/bimtracker/api/allrecords/all")
      .then(function (response) {
        getProgress(response.data);
      });

    //Check for ActiveBIM status
  };

  const getProgress = (data) => {
    const records = data.map((record) => {
      let completePeople = false;
      let completeStorage = false;
      let completeProcess = false;
      let completeCoordination = false;
      if (
        record.Approver_B_name &&
        record.Checker_B_name &&
        record.CoordinationManager &&
        record.Engineer_B_name
      ) {
        completePeople = true;
      }
      if (
        record.storage_CDE &&
        record.storage_C_Models &&
        record.storage_Clarity_BIM_360_Backup &&
        record.storage_MEP_Models &&
        record.storage_S_Models
      ) {
        completeStorage = true;
      }
      if (
        //record.process_BEP_Comments &&
        record.process_BEP_Status &&
        //record.process_Comments &&
        //record.process_EIR_Comments &&
        record.process_EIR_Status &&
        record.process_Information_Deliverables &&
        //record.process_Responsibility_Matrix_Comments &&
        record.process_Responsibility_Matrix_Status
      ) {
        completeProcess = true;
      }
      if (
        record.coordination_Internal_Coord_Meetings &&
        record.coordination_Issue_Tracking &&
        //record.coordination_Issue_Tracking_Other &&
        record.coordination_Whole_Team_Coord_Meetings
      ) {
        completeCoordination = true;
      }
      return {
        ...record,
        completePeople,
        completeStorage,
        completeProcess,
        completeCoordination,
      };
    });
    setMyRecords(records);
  };

  const handleExpandTables = () => {
    setExpandTables(!expandTables);
  };

  const handleOpenProject = (projectCode) => {
    let url =
      "https://bmtools.arup.com/bimtracker?projectFilter=" + projectCode;
    window.open(url);
  };

  const handleOpenProjectDiscipline = (projectCode, discipline) => {
    let url =
      "https://bmtools.arup.com/bimtracker?projectFilter=" +
      projectCode +
      "&discipline=" +
      discipline;
    window.open(url);
  };

  useEffect(() => {
    getRecords();
    const interval = setInterval(() => {
      getRecords();
    }, 60000);
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    if (myRecords.length > 0) {
      let newBIMLeadRecords = [];
      let newBIMCheckerApproverRecords = [];
      myRecords.map((record) => {
        // if (record.Engineer_B_name === userData.account.name) {
        if (record.Engineer_B_name === "Colin Magner") {
          newBIMLeadRecords.push(record);
        }
        // if (
        //   record.Checker_B_name === userData.account.name ||
        //   record.Approver_B_name === userData.account.name
        // ) {
        if (
          record.Checker_B_name === "Colin Magner" ||
          record.Approver_B_name === "Colin Magner"
        ) {
          newBIMCheckerApproverRecords.push(record);
        }
      });
      setBimLeadRecords(newBIMLeadRecords);
      setBimCheckerApproverRecords(newBIMCheckerApproverRecords);
    }
  }, [myRecords]);

  return {
    myRecords,
    bimLeadRecords,
    bimCheckerApproverRecords,
    handleExpandTables,
    expandTables,
    handleOpenProject,
    handleOpenProjectDiscipline,
  };
};
