import React from "react"

//Styles

//Material UI Components
import Typography from '@material-ui/core/Typography';
import Box from '@mui/material/Box';

export default function ComingSoon() {
    return (
        <div>
            <Typography variant="h6">Coming Soon</Typography>
            <br />
            <Typography variant="body2">Trackers such as 30 day review, PM/PD tools and more</Typography>
            <Typography variant="body2">Possible PaT integration. Plan your time from your Desktop!</Typography>
            <Box sx={{height: 200}}/>
        </div>
    );
}