import React from "react"

//Hooks

//Material UI Components
import Typography from '@material-ui/core/Typography';

export default function Welcome() {
    return (
        <div>
            <Typography variant="h6"><b>Welcome to your Hub</b></Typography>
            <Typography variant="subtitle1" align="left" style={{marginLeft: '3%'}}>Changelog:</Typography>
            <Typography variant="body2" align="left" style={{marginLeft: '5%'}}>
            <b>02/11/21</b><br />
            <b>-</b> Styles Overhaul! It's all so shiny!<br />
            <b>-</b> '?' tips added to each card<br />
            <b>01/11/21</b><br />
            <b>-</b> Added Project Role Tracker <br />
            <b>-</b> Backgrounds are available in the Account tab! Spice up your hub now!<br />
            </Typography>
        </div>
    );
}