import React from "react"

//Hooks
import useHooks from './hooks/use-hooks.js'

//Material UI Components
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@mui/material/Tooltip';
import Grid from '@material-ui/core/Grid';

import HelpOutlineIcon from '@mui/icons-material/HelpOutline';

//Styles
import useStyles from './my-projects-styles.js';

export default function MyProjects(props) {
    const {
        userData
    } = props;
    const {
        myProjects,
        loading,
    } = useHooks(userData);
    const classes = useStyles();
    return (
        <div>
            <Grid container direction="row" alignItems="center">
                <Grid item xs={11}>
                <Typography variant="h6">Project Role Tracker</Typography>
                </Grid>
                <Grid item xs={1}>
                    <Tooltip title={
                        <span className={classes.helpTooltipSpan}>
                            This Card contains your projects and the role that you are fulfilling.
                        </span>}
                        placement="bottom-end"
                    >
                        <HelpOutlineIcon fontSize="small" className={classes.helpIcon} />
                    </Tooltip>
                </Grid>
            </Grid>
            {!loading && myProjects.length > 0 && (
            <TableContainer component={Paper} className={classes.table}>
                <Table stickyHeader size="small">
                    <TableHead>
                        <TableRow>
                            <TableCell>Project Name</TableCell>
                            <TableCell>My Roles</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {myProjects.map((project) => (
                            <TableRow key={project.ProjectCode}>
                                <TableCell className={classes.titleCell}>
                                    <Tooltip title={project.JobNameShort} placement="top" className={classes.titleTooltip} enterDelay={400}>
                                        <Typography variant="body2" noWrap>{project.JobNameShort}</Typography>
                                    </Tooltip>
                                </TableCell>
                                <TableCell>{project.myRoles.map((role) => (role)).join(", ")}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
            )}
            {myProjects.length === 0 &&
            <Typography variant="body2">
                It looks like you aren't on any Projects <br />
                Visit the Project Tracker <a href="https://projectapprovalstracker.arup.com/" target="_blank" rel="noreferrer" >here</a> and check back later.
            </Typography>
            }
        </div>
    );
}