import { useState, useEffect } from "react";
const axios = require("axios");

export default (userData) => {
  const [myProjects, setMyProjects] = useState([]);
  const [loading, setLoading] = useState(true);

  const getData = () => {
    setLoading(true);
    axios
      .get("https://projectapprovalstracker.arup.com/api/getpwadata")
      .then(function (response) {
        getMyProjects(response.data);
      });
  };

  const getMyProjects = (allData) => {
    let newProjects = [];
    allData.currentData.map((project) => {
      // if (Object.values(project).includes(userData.account.name)) {
      if (Object.values(project).includes("Colin Magner")) {
        newProjects.push(project);
      }
    });
    setMyProjects(newProjects);
  };

  const getMyRoles = () => {
    myProjects.map((project) => {
      let myRoles = [];
      for (const prop in project) {
        // if (project[prop] === userData.account.name) {
        if (project[prop] === "Colin Magner") {
          let displayProp = "";
          switch (prop) {
            case "Approver_B_name":
              displayProp = "Approver BIM";
              break;
            case "Approver_C_name":
              displayProp = "Approver Civil";
              break;
            case "Approver_E_name":
              displayProp = "Approver Electrical";
              break;
            case "Approver_M_name":
              displayProp = "Approver Mechanical";
              break;
            case "Approver_P_name":
              displayProp = "Approver Public Health";
              break;
            case "Approver_S_name":
              displayProp = "Approver Structural";
              break;
            case "Checker_B_name":
              displayProp = "Checker BIM";
              break;
            case "Checker_C_name":
              displayProp = "Checker Civil";
              break;
            case "Checker_E_name":
              displayProp = "Checker Electrical";
              break;
            case "Checker_M_name":
              displayProp = "Checker Mechanical";
              break;
            case "Checker_P_name":
              displayProp = "Checker Public Health";
              break;
            case "Checker_S_name":
              displayProp = "Checker Structural";
              break;
            case "Engineer_B_name":
              displayProp = "Lead BIM";
              break;
            case "Engineer_C_name":
              displayProp = "Lead Civil";
              break;
            case "Engineer_E_name":
              displayProp = "Lead Electrical";
              break;
            case "Engineer_M_name":
              displayProp = "Lead Mechanical";
              break;
            case "Engineer_P_name":
              displayProp = "Lead Public Health";
              break;
            case "Engineer_S_name":
              displayProp = "Lead Structural";
              break;
            case "ProjectDirectorName":
              displayProp = "Project Director";
              break;
            case "ProjectManagerName":
              displayProp = "Project Manager";
              break;
            default:
              displayProp = prop;
          }
          myRoles.push(displayProp);
        }
      }
      project.myRoles = myRoles;
    });
    setMyProjects(myProjects);
    setLoading(false);
  };

  useEffect(() => {
    getData();
  }, []);

  useEffect(() => {
    if (myProjects.length > 0) {
      getMyRoles();
    }
  }, [myProjects]);

  return {
    myProjects,
    loading,
  };
};
